@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;600&family=Quicksand:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Open Sans', sans-serif;
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
.navbar__details__container {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
}

.navbar__details__title {
    display: flex;
    flex-direction: row;
    flex-grow: 1.5;
    justify-content: space-around;
    flex-wrap: wrap;
    position: relative;
}

.navbar__details__cloudy {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    justify-content: space-around;
    flex-wrap: wrap;
}

.navbar__details__Humidity {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    justify-content: space-around;
    flex-wrap: wrap;
}

.navbar__details__Wind {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    justify-content: space-around;
    flex-wrap: wrap;
}

.navbar__details__Rain {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    justify-content: space-around;
    flex-wrap: wrap;
}

.navbar__details__container h3 {
    color: white;
}

.title h3 {
    color: white;
}

@media only screen and (max-width: 1540px) {
    .navbar__details__title {
        position: relative;
        left: 15px;
    }
    .navbar__details__cloudy {
        position: relative;
        left: 10px;
    }
    .navbar__details__Humidity {
        position: relative;
        left: 10px;
    }
    .navbar__details__Wind {
        position: relative;
        left: 10px;
    }
}

@media only screen and (min-width: 1600px) {
    .navbar__details__title {
        position: relative;
        left: 40px;
    }
    .navbar__details__cloudy {
        position: relative;
        left: 40px;
    }
    .navbar__details__Humidity {
        position: relative;
        left: 40px;
    }
    .navbar__details__Wind {
        position: relative;
        left: 40px;
    }
}
.weather__navbar {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4);
    /* Black w/opacity/see-through */
}

.weather__input__container {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    opacity: 100;
}

.navbar__input {
    background-color: rgba(180, 180, 180, 0.2);
    width: 72%;
}

::-webkit-input-placeholder {
    font-size: 1.5em;
}

:-ms-input-placeholder {
    font-size: 1.5em;
}

::-ms-input-placeholder {
    font-size: 1.5em;
}

::placeholder {
    font-size: 1.5em;
}

.navbar__searchIcon {
    position: relative;
    padding: 31px;
    background-color: #829F9A;
}

.navbar__cities__container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    flex-grow: 1;
}

.cities {
    display: flex;
    flex-direction: column;
    flex-grow: 0.5;
    justify-content: space-evenly;
    color: white;
}

.cities h3 {
    position: relative;
    left: 0.8vw;
}

@media only screen and (min-width: 1600px) {
    .cities h3 {
        position: relative;
        left: 35px;
    }
}
.weather__container {
    display: flex;
    flex-direction: column;
    flex-grow: 2;
    justify-content: space-around;
    flex-wrap: wrap;
}

.the__weather__section {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    justify-content: space-evenly;
    flex-wrap: wrap;
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.2);
    /* Black w/opacity/see-through */
}

.the__weather__title {
    display: flex;
    justify-content: space-around;
}

.weather__info {
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    justify-content: space-around;
    justify-content: space-around;
    background-color: rgb(0, 0, 0);
    /* Fallback color */
    background-color: rgba(0, 0, 0, 0.2);
    /* Black w/opacity/see-through */
    color: white;
}

.weather__degree {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
}

.weather__degree h1 {
    font-size: 100px;
    color: white;
}

.weather__city h1 {
    font-size: 50px;
    font-weight: 1100;
    color: white;
}

.weather__city sub {
    font-size: 13px;
    position: relative;
    bottom: 25px;
    text-align: center;
    font-weight: 1000;
    color: white;
}

.weather__city {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    position: relative;
    right: 3.5vw;
}

.weather__logo {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
}

.weather__logo {
    position: relative;
    right: 7vw;
    bottom: 0.2vw;
    align-items: center;
}

.weather__logo img {
    max-width: 80px;
    max-height: 80px;
    position: relative;
    bottom: 20px;
}

.weather__logo sub {
    font-size: 13px;
    position: relative;
    text-align: center;
    top: 3px;
    font-weight: 1000;
}
body {
  overflow: hidden;
  /* Hide scrollbars */
}

.App {
  display: flex;
  height: 100vh;
}

.App {
  background: no-repeat center center fixed;
  background-size: cover;
}
